<template lang="pug">
.page.auth-page
  .hp-at-area
    .hp-at-hint
      .hp-at-border
        input.hp-at-input(
          type="password"
          v-model="accessToken"
          placeholder="請輸入存取碼"
          @keyup="onInputKeyUp"
        )
        button.hp-at-btn(@click="onVerify")
          | 驗證
      br
      span.hp-at-msg(v-show="msg !== ''")
        | {{ msg }}
</template>
<script>
import Cookies from 'js-cookie';
import * as AtChecker from '@/core/at_checker';

export default {
  data() {
    return {
      accessToken: '',
      msg: '',
    };
  },
  computed: {
    filteredSkills() {
      let results;
      if (this.queryKeyword === '') {
        results = this.skills;
      } else {
        results = this.skills.filter(skill => this.findKeyword(skill, this.queryKeyword));
      }
      return results;
    },
  },
  methods: {
    onInputKeyUp(e) {
      if (e.keyCode === 13) {
        this.onVerify();
      }
    },
    onVerify() {
      const result = AtChecker.verify(this.accessToken);
      if (result === 'ok') {
        Cookies.set('at', this.accessToken);
        this.$router.push('/experience');
      } else if (result === 'expired') {
        this.msg = '驗證碼已過期，請直接聯絡本人。';
      } else {
        this.msg = '驗證失敗，請重新輸入證驗碼。';
      }
      this.accessToken = '';
    },
  },
};
</script>
