import AtData from '@/data/at.json';

export const verify = (givenAt) => {
  const now = new Date();
  let result = 'invalid';
  AtData.forEach((at) => {
    if (at.code === givenAt) {
      if (Date.parse(at.expired_at) > now) {
        result = 'ok';
      } else {
        result = 'expired';
      }
    }
  });
  return result;
};

export { verify as default };
