<template lang="pug">
.page.skill-page
  h2
    | 技能等級
  .sp-star-table
    .sp-st-line
      i.fa.fa-fw.fa-star
      |
      | 忘光了 - 有使用過的經驗但很久沒有用，相關知識已經忘光光了。但比完全不知道強，可以再短時間恢復到初學的等級。
    .sp-st-line
      i.fa.fa-fw.fa-star
      i.fa.fa-fw.fa-star
      |
      | 知道 - 有3個月以上的持繼使用經驗，比初學者強，經過適當的培訓可以進行相關的工作。
    .sp-st-line
      i.fa.fa-fw.fa-star
      i.fa.fa-fw.fa-star
      i.fa.fa-fw.fa-star
      |
      | 熟練 - 有1年以上的持繼使用經驗，了解相關的知識並且實際使用在日常的工作之中。
    .sp-st-line
      i.fa.fa-fw.fa-star
      i.fa.fa-fw.fa-star
      i.fa.fa-fw.fa-star
      i.fa.fa-fw.fa-star
      |
      | 精通 - 累積很長一段時間的持繼使用經驗，並且持繼接收精進相關的知識。
    .sp-st-line.sp-st-del
      i.fa.fa-fw.fa-star
      i.fa.fa-fw.fa-star
      i.fa.fa-fw.fa-star
      i.fa.fa-fw.fa-star
      i.fa.fa-fw.fa-star
      |
      | 大師 - 應該是沒有人比我強。
  h2
    | 技能列表
  .sp-skill-query
    input(type="search" v-model="queryKeyword" placeholder="請輸入關鍵字")
  .sp-skill-list
    .sp-sl-entry(v-for="skill in filteredSkills")
      .sp-sl-name
        | {{ skill.name }}
        .sp-sl-tags
          .sp-sl-tag(v-for="tag in skill.tags")
            | {{ tag }}
      .sp-sl-star
        i.fa.fa-fw.fa-star(v-for="n in parseInt(skill.star.split('.')[0])")
        i.fa.fa-fw.fa-star-half-alt(v-show="skill.star.split('.').length === 2")
      .sp-sl-comment
        p
          | {{ skill.comment }}
        ul
          li(v-for="point in skill.points")
            span(v-show="point.split('=').length !== 2")
              | {{ point }}
            span(v-show="point.split('=').length === 2")
              a(:href="point.split('=')[1]" target="_blank")
                | {{ point.split('=')[0] }}
</template>
<script>
import SkillData from '@/data/skill.json';

export default {
  data() {
    return {
      skills: SkillData,
      queryKeyword: '',
    };
  },
  computed: {
    filteredSkills() {
      let results;
      if (this.queryKeyword === '') {
        results = this.skills;
      } else {
        results = this.skills.filter(skill => this.findKeyword(skill, this.queryKeyword));
      }
      return results;
    },
  },
  methods: {
    findKeyword(skill, keyword) {
      const kw = keyword.toLowerCase();
      return (
        skill.name.indexOf(kw) >= 0
        || skill.comment.indexOf(kw) >= 0
        || skill.tags.join(',').indexOf(kw) >= 0
        || skill.points.join(',').indexOf(kw) >= 0
        || skill.star.indexOf(kw) >= 0
      );
    },
  },
};
</script>
